@import url(https://fonts.googleapis.com/css?family=Montserrat:600|Noto+Sans|Open+Sans:400,700&display=swap);
.login-wrapper {
    display: flex;
    flex-wrap: nowrap;
    height: 95vh;
    width: 95vw;
    align-items: center;
    justify-items: center;
}

.login-wrapper>.login-img {
    background: url('/images/LoginSplash.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 25vw;
    margin-left: 12.5vw;
    height: 80vh;
}

.login-wrapper>.login-form {
    width: 50vw;
    margin-right: 12.5vw;
    height: 80vh;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.login-span {
    float: right;
    margin-top: 30px;
    margin-right: 30px;
    font-family: Verdana, sans-serif;
    color: white;
}

.login-form-wrapper {
    width: 50%;
    margin: auto;
    height: 70%;
    margin-top: 15%;
    color: white;
}

.login-form-wrapper h1 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 38px;
    color: white;
}

.login-form-wrapper p {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    margin-top: 30px;
    color: white;
    margin-bottom: 30px;
}

.input-container {
    position: relative;
    background: white;
}

.login-btn {
    background: #fff;
    color: #419FE6;
    border-radius: 25px;
    padding: 15px 40px;
    margin-left: 0px;
    font-size: large;
    cursor: pointer;
    border: none;
}

.login-btn:hover {
    background: linear-gradient(to bottom, #ef027d 5%, #ff5bb0 100%);
    background-color: #ef027d;
    color: white;
}

.login-btn:active {
    position: relative;
    top: 1px;
}

.input-field {
    margin: 25px 0;
    position: relative;
    height: 50px;
    width: 100%;
}

.input-field input {
    height: 100%;
    width: 100%;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: none;
    padding-left: 15px;
    outline: none;
    font-size: 19px;
    transition: .4s;
}

input:focus {
    border-left: 4px solid #f315a9;
}

.input-field label,
span.show {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.input-field label {
    left: 15px;
    pointer-events: none;
    color: grey;
    font-size: 14px;
    transition: .4s;
}

span.show {
    right: 20px;
    color: #111;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    visibility: hidden;
    font-family: 'Open Sans', sans-serif;
}

input:valid~span.show {
    visibility: visible;
}

input:focus~label,
input:valid~label {
    transform: translateY(-33px);
    background: white;
    font-size: 16px;
    color: grey;
}
nav{
    height: 100px;
    width: 100%;
  }
  label.logo{
    color: #f2f2f2;
    font-size: 33px;
    line-height: 80px;
    padding: 0 140px;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
  }
  nav ul{
    float: right;
    margin-right: 60px;
  }
  nav ul li{
    display: inline-block;
    line-height: 120px;
    margin: 0 2px;
  }
  nav ul li a{
    color: #f2f2f2;
    font-weight: 500;
    font-size: 16px;
    padding: 7px 13px;
    text-decoration: none;
    /* text-transform: uppercase; */
    font-family: 'Poppins', sans-serif;
  }

  nav .username { 
      position: absolute;           
      top: -40px;      
      right: 80px;
      color: #fff;      
      height: 70px;
  }

  nav ul li .logout {
      background: #fff;
      color: #419FE6;
      border-radius: 25px;
      padding: 5px 30px;
      margin-left: 30px;
      z-index: 1;
      cursor: pointer;
  }

  a.active,a:hover{
    border-bottom: 1px solid #fff;
    transition: .5s;
  }
  .checkbtn{
    font-size: 30px;
    color: white;
    float: right;
    line-height: 80px;
    margin-right: 40px;
    cursor: pointer;
    display: none;
  }
  #check{
    display: none;
  }
  @media (max-width: 952px){
    label.logo{
      font-size: 27px;
      padding-left: 25px;
    }
    nav ul li a{
      font-size: 16px;
    }
  }
  @media (max-width: 858px){
    .checkbtn{
      display: block;
      margin-right: 40px;
    }
    ul{
      position: fixed;
      width: 100%;
      height: 100vh;
      background: #2c3e50;
      top: 80px;
      left: -100%;
      text-align: center;
      transition: all .5s;
    }
    nav ul li{
      display: block;
      margin: 50px 0;
      line-height: 30px;
    }
    nav ul li a{
      font-size: 20px;
    }
    a:hover,a.active{
      background: none;
      color: #0082e6;
    }
    #check:checked ~ ul{
      left: 0;
    }
  }
